@font-face {
  src: local('Kooka-Regular'), url(/2-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

html {
  background-color: black;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Kooka-Regular', Arial, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  src: local('Kooka-Regular'), url(/2-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

:root {
  /* root colors, these are all of the flowers in the canva logo i made */
  --dark-green: #5b846d;
  --orange: #ef7525;
  --lilac: #cb6ce6;
  --red: #ef1963;
  --purple: #8c52ff;
  --blue: #80bef8;
  --lime: #c9e265;
  --yellow: #ffde59;
}

.App {
  text-align: center;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  src: url(/2-be-wed/static/media/Kooka-Regular.2599e0b5.ttf);
  font-family: 'Kooka-Regular';
}

.navbar-nav {
  flex-wrap: wrap;
  justify-content: center;
}

.nav-link {
  font-family: 'Kooka-Regular';
  color: white !important;
  font-size: 1.5rem;
}

.nav-link:nth-child(1):focus {
  color: var(--dark-green) !important;
}
.nav-link:nth-child(2):focus {
  color: var(--orange) !important;
}
.nav-link:nth-child(3):focus {
  color: var(--lilac) !important;
}
.nav-link:nth-child(4):focus {
  color: var(--red) !important;
}
.nav-link:nth-child(5):focus {
  color: var(--purple) !important;
}
.nav-link:nth-child(6):focus {
  color: var(--blue) !important;
}
.nav-link:nth-child(7):focus {
  color: var(--lime) !important;
}
.nav-link:nth-child(8):focus {
  color: var(--yellow) !important;
}

.us-pic {
  border: 12px solid var(--dark-green);
  padding: 0;
  margin-top: 75px;
}

h2 {
  color: var(--lilac);
  font-family: Arial, Helvetica, sans-serif;
}

.accordion {
  width: 80%;
  margin: 0 auto;
  margin-top: 30px;
  border: 20px solid var(--lime);
  border-radius: 20px;
}

.accordion-body {
  background-color: var(--lime);
  color: black;
}

.accordion-button {
  background-color: var(--lilac) !important;
  color: black !important;
  font-weight: bold;
}

li {
  list-style: none;
}

img {
  width: 80%;
  margin: 0 auto;
  padding-top: 50px;
}

@media screen and (max-width: 390px) {
  img {
    width: 90%;
  }
}

